@import "common.scss";

.dialog{
  border: none;
  border-radius: 10px;
  box-shadow: 0 6px 20px  0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.2);
  width: 600px;
  height: 620px;
  opacity:0;
  .typeClass{
    margin-left: 10px;
    margin-right: 10px;
  }
  .typeClassSpan{
    display: inline-block;
    width: 450px;
  }
  label{
    display:inline-block;
    width: 100px;
    margin-left: 30px;
  }
  button{
    width: 250px;
    height: 45px;
    border-radius: 7px;
    background-color: #027FD4;
    color: white;
    margin-right: 10px;
    margin-top: 10px;
    border: 0;
    &:hover{
      background-color: #599dca;
    }

  }
  .dialogButton{
    margin: 0 0 0 40px;
  }
  input{
    width: 200px;
    height: 20px;
    padding: 5px 10px;
    border: 1px solid #a7a7a7;
    border-radius: 3px;
    margin-bottom: 10px;
  }
  select{
    margin-bottom: 10px;
    padding: 5px 10px;
  }
  textarea{
    margin-bottom: 10px;
    border: 1px solid #a7a7a7;
    border-radius: 3px;
  }
  h3{
    display: inline;
    margin: 0 290px 0 20px;
    padding: 0;
  }
  .corporateName{
    width: 300px;
  }
  .phoneNumber{
    width: 100px;
  }
  .peopleOfStay{
    width: 50px;
  }
  .price{
    width: 70px;
  }
  .memo{
    margin-left: 30px;
    margin-top: 10px;
    margin-bottom: 10px;
  }
  .dialogMain{
    margin-top: 25px;
    margin: 25px 25px 0 25px;
  }
}

dialog::backdrop {
  background: rgba(0, 0, 0, 0.5);
}


dialog[open] {   
  visibility:visible;
  opacity:1;
  transition-delay:1s;
 } 