.main-content {
  display: inline-block;
  padding: 10px 20px;
  table,
  th,
  td {
    border-collapse: collapse;
    border: 1px solid rgba(0, 0, 0, 0.608);
    table-layout: fixed;
    font-size: small;
  }

  th {
    background-color: #53b5ff;
  }
  tr:hover{
    background-color: #d3d3d3b0; 
    cursor: pointer;
  }
  .wid-10{
    width: 10px;
  }
  .wid-30{
    width: 30px;
  }
  .wid-35{
    width: 35px;
  }
  .wid-100{
    width: 100px;
    text-align: center;
  }
  .wid-150{
    width: 150px;
  }
  .wid-20{
    width: 20px;
    text-align: center;
  }
  .wid-70{
    width: 70px;
  }
  .price{
    width: 70px;
    text-align: center;
  }
  .date-of-payment{
    width: 85px;
    text-align: center;
  }
  .payment-flg{
    width: 20px;
    text-align: center;
  }
  .memoTable{
    width: 300px; 
    overflow: hidden;
    text-overflow: ellipsis; 
    white-space: nowrap;
    max-width: 300px; 
  }
}

.task-content-item {
  margin: 20px 20px 10px 20px;
  vertical-align: top;
}
.task-content-button {
  width: 300px;
  margin: 0 0 0 500px;
}
.task-list-update_button {
  display: inline-block;
}

.headerBar{
  display: inline-block;
  width: 500px;
  height: 50px;
  background-color: #0000008a;
}

.content-data{
  display: inline-block;
  overflow:scroll;
  height: calc(100vh - 200px)  ;
}

.content-header-day{
  font-size: large;
}

.content-button{
  button{
    width: 120px;
    height: 30px;
    border-radius: 7px;
    background-color: #027FD4;
    color: white;
    margin-right: 10px;
    margin-top: 10px;
    border: 0;
    &:hover{
      background-color: #599dca;
    }
  }
}

