@import "common.scss";

.header-bar{
  display: flex;
  justify-content: space-between;
  width: $header-width;
  height: 35px;
  background-color: $headerBar-color;
  button{
    background-color: $headerBar-color;
    border: 1px solid $headerBar-color;
    color: white;
    opacity: 0.8;
    &:hover{
      background-color: rgb(68, 68, 72);
      opacity: 1;
    }
  }
}

.header-bar-day{
  display: inline-block;
  margin-right: 70px;
  margin-left: 10px;
  margin-top: 0px;
  margin-bottom: 0;
  padding: 5px 2px;
}

.header-bar-today{
  margin-right: 20px;
  margin-left: 10px;
  margin-top: 0px;
  margin-bottom: 0;
  padding: 5px 2px;
}

.selectedStyle{
  button{
    background-color: white;
    border: 1px solid $headerBar-color;
    color: white;
    opacity: 0.8;
    &:hover{
      background-color: rgb(68, 68, 72);
      opacity: 1;
    }
  }
}