@import "common.scss";

.task-list {
  width: $sideNavBar-width;
  height: 100vh;
  background-color:$sideNavBar-color;
  display: inline-block;
  color: #fff;
  font-size: 13px;
  &-title{
    display: flex;
    justify-content: space-between;
    font-size: 0.9em;
    color: rgba(255, 255, 255, 0.429);
    padding: 5px 0 0 10px;
    height: 3%;
    p{
      margin: 0;
    }
    &-fileicon{
      img{
        opacity: 0.7;
      }
      div{
        width:13px;
        display: inline-block;
        margin-right: 5px;
        padding: 2px 2px 0px 2px;
        &:hover{
          background-color: rgb(68, 68, 72);
        }
      }
    }
  }
  &-main{
    height: 96%;
  }
}

