@import "common.scss";

.side-ber {
  background-color: $sideber-color;
  width: $sideber-width;
  height: 100vh;
  overflow: hidden;
  display: inline-block;
  img{
    width: 25px;
    margin-top: 10px;
    margin-left: 1px;
  }
  div{
    text-align: center;
  }
  &-div-normal {
    .img-button {
      opacity: 0.3;
      &:hover {
        opacity: 0.8;
      }
    }
  }
  &-div-click {
    border-left: solid #fff 1px;
    .img-button {
      margin-left: 0;
      opacity: 0.8;
    }
  }
}
